/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommerceMarksGeneralStyle() {
    position: absolute;
    display: block;
    top: 0;
    width: auto;
    height: 38px;
    margin: 0;
    padding: 0 20px;
    color: $default-text-color;
    font-size: 11px;
    text-transform: uppercase;
    line-height: 38px;
    letter-spacing: 0.2em;
    text-align: center;
    z-index: 100;
    box-sizing: border-box;
    @include eltdfTransform(translateZ(0));
}

@mixin eltdfWooCommerceSaleStyle() {
    left: 0;
    background-color: $default-background-color;
}

@mixin eltdfWooCommerceOutOfStockStyle() {
    left: 0;
    background-color: $default-background-color;
}

@mixin eltdfWooCommerceNewProductStyle() {
    left: 0;
    background-color: $default-background-color;
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin eltdfWooCommercePriceStyle() {
    position: relative;
    display: block;
    margin: 12px 0 0;
    line-height: 1;
    color: $default-text-color;
    
    * {
        font-size: inherit;
        line-height: inherit;
    }
    
    del {
        margin: 0 8px 0 0;
        color: rgba($default-text-color, .8);
    }
    
    ins {
        text-decoration: none;
    }
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
    font-family: "Ionicons";
    font-size: 14px;
    line-height: 1;
    letter-spacing: 5px;
    color: #b9b9b9;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

@mixin eltdfWooCommerceRatingsStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    @extend %star-ratings;
    overflow: hidden;
    
    &:before {
        content: "\f4b2\f4b2\f4b2\f4b2\f4b2";
    }
    
    span {
        position: absolute;
        top: 0;
        left: 0;
        overflow: hidden;
        
        &:before {
            content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        }
    }
}

@mixin eltdfWooCommerceSingleProductRatingsStyle() {
    
    &:before {
        content: "\f4b2";
        position: relative;
        display: inline-block;
        vertical-align: middle;
        @extend %star-ratings;
        overflow: hidden;
        z-index: -1;
    }
    
    &.active:after {
        content: "\f4b3\f4b3\f4b3\f4b3\f4b3";
        position: absolute;
        right: 0;
        bottom: 6px;
        display: block;
        @extend %star-ratings;
    }
}
/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */