/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.eltdf-pl-holder {
    @include eltdfRelativeHolderLayout();
	
	&.eltdf-info-on-image {
		
		.eltdf-pli-inner {
			
			&:hover {
				.eltdf-pli-image {
					&:after {
						opacity: 1;
					}
				}
			}
		}
		
		.eltdf-pli {
			
			.eltdf-pli-add-to-cart {
				margin: 25px 0 0;
			}
		}
		
		.eltdf-pli-image {
			
			&:after {
				position: absolute;
				content: '';
				top: 0;
				left: 0;
				width: 100%;
				height: 100%;
				background-color: rgba($default-heading-color, .2);
				opacity: 0;
				@include eltdfTransition(opacity .5s ease);
			}
		}
	}

    .eltdf-pli {
        padding: 0;
        z-index: 100;

	    .eltdf-pli-title {
		    margin: 0 0 2px;
	    }
	
	    .eltdf-pli-category {
		    margin: 11px 0 3px;
	    }

        .eltdf-pli-excerpt {
            color: $default-heading-color;
	        line-height: 1.3em;
            margin: 11px 0 3px;
        }

	    .eltdf-pli-rating-holder {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 2px 0 3px;
	    }

	    .eltdf-pli-rating {
		    @include eltdfWooCommerceRatingsStyle();
	    }

        .eltdf-pli-price {
	        @include eltdfWooCommercePriceStyle();
        }

	    .eltdf-pli-link {
		    @include eltdfAbsoluteHolderLayout();
	    }

	    &:hover {

            .eltdf-pli-text {
	            bottom: -1px;
	            opacity: 1;
            }

	        .eltdf-pli-image {
		        &:after {
			        opacity: 1;
		        }
	        }
        }
    }

    .eltdf-pli-inner {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        overflow: hidden;

        .touch & {
            cursor: pointer;
        }

        .eltdf-pli-image {
            @include eltdfRelativeHolderLayout();

            img {
                display: block;
                width: 100%;
            }

            .eltdf-pli-onsale,
            .eltdf-pli-out-of-stock,
            .eltdf-pli-new-product {
	            @include eltdfWooCommerceMarksGeneralStyle();
            }

            .eltdf-pli-onsale {
	            @include eltdfWooCommerceSaleStyle();
            }

            .eltdf-pli-out-of-stock {
	            @include eltdfWooCommerceOutOfStockStyle();
            }
	
	        .eltdf-pli-new-product {
		        @include eltdfWooCommerceNewProductStyle();
	        }
        }
            
        .eltdf-pli-text {
            position: absolute;
            left: 0;
            bottom: -50px;
            width: 100%;
	        height: 100%;
	        opacity: 0;
            @include eltdfTransition(bottom .5s ease, opacity .5s ease);
        }

        .eltdf-pli-text-outer {
	        @include eltdfTableLayout();
	        text-align: center;
        }

        .eltdf-pli-text-inner {
            @include eltdfTableCellLayout();
	        vertical-align: bottom;

            .eltdf-pli-title,
            .eltdf-pli-category a,
            .eltdf-pli-excerpt,
            .eltdf-pli-rating,
            .eltdf-pli-price {
                color: #fff;
            }
	
	        .eltdf-pli-category a:hover {
		        color: rgba(#fff, .8);
	        }

	        .eltdf-pli-add-to-cart {

		        &.eltdf-default-skin {

			        .button,
			        .added_to_cart {
				        @include eltdfButtonSolidColor();

				        &:hover {
					        color: $default-heading-color;
					        background-color: $default-background-color;
					        border: 1px solid $default-background-color;
				        }
			        }
		        }

		        &.eltdf-light-skin {

			        .button,
			        .added_to_cart {
				        color: $default-heading-color;
				        background-color: #fff;

				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
		
		        &.eltdf-dark-skin {
			
			        .button,
			        .added_to_cart {
				        color: #fff;
				        background-color: $default-heading-color;
				
				        &:hover {
					        color: #fff;
					        background-color: $first-main-color;
				        }
			        }
		        }
	        }

            .button,
            .added_to_cart {
	            @include eltdfButtonDefaultStyle();
	            @include eltdfButtonSize(huge);
	            white-space: nowrap;
	            width: calc(100% + 1px);
	            z-index: 3;
            }

            .button.added {
                display: none;
            }
        }
    }

    .eltdf-pli-text-wrapper {
        @include eltdfRelativeHolderLayout();
        margin: 17px 0 0;
        text-align: center;
    }
	
	&.eltdf-product-info-light {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: #fff;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.eltdf-product-info-dark {
		
		.eltdf-pli-inner {
			
			.eltdf-pli-text-inner {
				
				.eltdf-pli-title,
				.eltdf-pli-category a,
				.eltdf-pli-excerpt,
				.eltdf-pli-rating,
				.eltdf-pli-price {
					color: $default-heading-color;
				}
				
				.eltdf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.eltdf-masonry-layout {
		
		.eltdf-pli-inner {
			width: 100%;
			height: 100%;
			
			.eltdf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}
/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */