/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .eltdf-content,
div.woocommerce {
    
    input[type="submit"],
    button[type="submit"]:not(.eltdf-woo-search-widget-button):not(.eltdf-search-submit),
    .wc-forward:not(.added_to_cart) {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonOutlineColor();
        padding: 14px 46px;
        border-radius: 0;
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include eltdfButtonOutlineHoverColor();
        }
    }

    a.button,
    a.added_to_cart {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonSize(huge);
        @include eltdfButtonSolidColor();
        cursor: pointer;
        z-index: 3;

        &:hover {
            background-color: $default-background-color;
            color: $default-heading-color;
        }
    }
    
    a.button.added {
        display: none;
    }
}

.eltdf-woocommerce-page.woocommerce-account {

    a.button {
        @include eltdfButtonDefaultStyle();
        @include eltdfButtonOutlineColor();
        padding: 14px 46px;
        cursor: pointer;
        z-index: 3;

        &:hover {
            @include eltdfButtonOutlineHoverColor();
        }
    }
}
/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */