/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.eltdf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }

    .eltdf-shopping-cart-inner {
        position: relative;
        display: table;
        height: 100%;
    }

    .eltdf-header-cart {
        position: relative;
        display: table-cell;
        vertical-align: middle;
        font-family: $default-heading-font;
        color: $default-heading-color;
        font-size: 13px;
        line-height: 23px;
        letter-spacing: .1em;
        box-sizing: border-box;

        .eltdf-cart-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
        }

        &.eltdf-header-cart-icon-pack {
            
            .eltdf-cart-icon {
                font-size: 20px;
                line-height: 1em;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.eltdf-header-cart-svg-path {
            
            .eltdf-cart-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }

    &:hover {
	
	    .eltdf-shopping-cart-dropdown {
		    visibility: visible;
		    max-height: 800px;
	    }
    }
}

.eltdf-dark-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.eltdf-light-header .eltdf-page-header > div:not(.eltdf-sticky-header):not(.fixed) .eltdf-shopping-cart-holder .eltdf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.eltdf-shopping-cart-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    width: 308px;
    max-height: 0;
    overflow: hidden;
    background-color: #000;
    border: 1px solid #565656;
    text-align: left;
    z-index: 1000;
    visibility: hidden;
    box-sizing: border-box;
    @include eltdfTransition(max-height .5s ease, visibility .5s ease);

    ul {
        margin: 25px;
        padding: 0;

        li {
            @include eltdfTableLayout();
            padding: 20px 0;
            border-bottom: 1px solid #565656;
            box-sizing: border-box;

            &.eltdf-empty-cart {
                color: #8f8f8f;
                border-bottom: 0;
                font-size: 13px;
            }

            &:first-child {
                padding-top: 0;
            }

            &:last-child {
                padding-bottom: 0;
                border-bottom: 0;
            }
        }
    }

    .eltdf-item-image-holder {
        position: relative;
        display: table-cell;
        height: 100%;
        width: 84px;
        vertical-align: top;

        img {
            display: block;
        }
    }

    .eltdf-item-info-holder {
        @include eltdfTableCellLayout();
        vertical-align: top;
        padding-left: 20px;

        .eltdf-product-title {
            display: block;
            margin: 0;
            padding: 0 20px 0 0;
            box-sizing: border-box;
            color: #fff;
            
            > a {
                position: relative;
    
                &:after {
                    position: absolute;
                    content: '';
                    top: 50%;
                    right: -6px;
                    width: 0;
                    height: 1px;
                    background-color: currentColor;
                    @include eltdfTransition(width .5s ease);
                    transition: width .5s ease;
                }
    
                &:hover {
                    color: #fff;
                    
                    &:after {
                        width: calc(100% + 12px);
                        left: -6px;
                    }
                }
            }
        }

        .remove {
            position: absolute;
            top: -1px;
            right: 0;
	        font-size: 22px;
	        line-height: 1;
            
            &:hover {
                color: #fff;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .eltdf-quantity {
            display: block;
            margin: 3px 0 0;
            font-size: 13px;
            color: #9c9c9c;
            font-weight: 400;
        }

        .amount {
            display: block;
            margin: 3px 0 0;
            color: #fff;
            font-size: 13px;
        }
    }

    .eltdf-cart-bottom {
        padding: 15px 0 0;

        .eltdf-subtotal-holder {
            position: relative;
            display: table;
            width: 100%;
            margin: 0 0 19px;

            > * {
                display: table-cell;
                vertical-align: top;
                line-height: 20px;
                font-size: 13px;
                color: #8f8f8f;
                font-weight: 400;
                letter-spacing: 0.05em;
            }

            .eltdf-total-amount {
                text-align: right;
            }
        }

        .eltdf-view-cart {
            @include eltdfRelativeHolderLayout();
            height: 44px;
            padding: 0 26px;
            color: #fff;
            background-color: transparent;
            border: 1px solid #565656;
            text-align: center;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out, background-color .2s ease-out);
            
            .eltdf-cart-btn-text {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: $default-heading-font;
                font-size: 12px;
                line-height: 40px;
                font-weight: inherit;
                letter-spacing: .14em;
                
                &:after {
                    position: absolute;
                    content: '';
                    top: 50%;
                    right: -6px;
                    width: 0;
                    height: 1px;
                    background-color: currentColor;
                    @include eltdfTransition(width .5s ease);
                    transition: width .5s ease;
                }
            }

            &:hover {
                
                .eltdf-cart-btn-text {
                    
                    &:after {
                        width: calc(100% + 12px);
                        left: -6px;
                    }
                }
            }
        }
    }
}

.eltdf-vertical-menu-area {

    .eltdf-shopping-cart-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */