/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
    clear: both;
    margin: 0 0 35px;
    
    &.widget_rating_filter,
    &.widget_shopping_cart,
    &.widget_layered_nav,
    &.widget_layered_nav_filters,
    &.widget_product_categories,
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products  {
        
        ul {
            padding: 0;
            margin: 0;
            list-style: none;
            
            li {
                @include eltdfRelativeHolderLayout();
                padding: 0;
                margin: 0 0 14px;
                clear: both;
                
                &:last-child {
                    margin: 0;
                }
                
                ul {
                    margin: 14px 0 0 10px;
                }
            }
        }
    }
    
    &.widget_shopping_cart {
        
        .widget_shopping_cart_content {
            @include eltdfRelativeHolderLayout();
            
            ul {
                
                li {
                    
                    img {
                        display: block;
                        max-width: 84px;
                        margin: 0 70% 13px 0;
                    }
                    
                    .remove {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 0;
                        line-height: 1;
                        
                        &:after {
                            position: relative;
                            display: block;
                            padding: 0 3px;
                            font-family: 'Ionicons';
                            content: "\f404";
                            font-size: 30px;
                            line-height: 25px;
                        }
                    }
                }
            }
            
            .variation {
                @include eltdfRelativeHolderLayout();
                
                > * {
                    display: inline-block;
                    vertical-align: middle;
                    margin: 0;
                    
                    p {
                        margin: 0;
                    }
                }
            }
            
            .total {
                margin: 17px 0 13px;
            }
            
            .buttons {
                margin: 10px 0 0;
            }
        }
    }
    
    &.widget_layered_nav {
    
        .eltdf-widget-title {
            margin: 0 0 17px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                
                &.chosen {
                    
                    a {
                        color: $first-main-color;
                    }
                }
            }
        }
    }
    
    &.widget_layered_nav_filters {
        
        .eltdf-widget-title {
            margin: 0 0 17px;
        }
    }
    
    &.widget_price_filter {

        .eltdf-widget-title {
            margin: 0 0 13px;
        }
        
        .price_slider_wrapper {
            
            .ui-widget-content {
                position: relative;
                height: 3px;
                background-color: #fefcf0;
                
                .ui-slider-handle {
                    position: absolute;
                    top: -4px;
                    height: 10px;
                    width: 10px;
                    background-color: #fff;
                    border: 1px solid $default-heading-color;
                    border-radius: 100%;
                    margin: 0 0 0 -1px;
                    cursor: pointer;
                    outline: medium none;
                    z-index: 2;
                    
                    &:last-child {
                        @include eltdfTransform(translateX(-50%));
                    }
                }
                
                .ui-slider-range {
                    position: absolute;
                    display: block;
                    height: 100%;
                    background-color: $default-heading-color;
                    z-index: 1;
                }
            }
        }
        
        .price_slider_amount {
            margin: 23px 0 0;
            
            .button {
                float: right;
                border-color: #e8e6dc !important;
                padding: 6px 32px !important;
                position: relative;
                top: -6px;
            }
            
            .price_label {
                float: left;
                font-size: 13px;
                letter-spacing: 0.05em;
            }
        }
    }
    
    &.widget_product_categories {

        .eltdf-widget-title {
            margin: 0 0 7px;
        }
        
        ul {
            
            li {
                margin: 2px 0;
                font-size: 13px;
                letter-spacing: 0.05em;
                
                ul {
                    margin: 0 0 0 10px;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_recent_reviews,
    &.widget_top_rated_products {
    
        .eltdf-widget-title {
            margin: 0 0 19px;
        }
        
        ul {
            
            li {
                min-height: 97px;
                margin: 0 0 10px;
                
                a {
                    
                    img {
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 100%;
                        max-width: 73px;
                    }
                    
                    span {
                        position: relative;
                        display: block;
                        padding: 0 0 0 85px;
                        line-height: 20px;
                        box-sizing: border-box;
                    }
                }
                
                > *:not(a) {
                    margin: 0 0 0 85px;
                    line-height: 20px;
                }
                
                .star-rating {
                    position: relative;
                    display: none;
                    
                    span {
                        max-width: 100px;
                    }
                }
                
                del,
                ins {
                    display: inline-block;
                    
                    span {
                        color: inherit;
                    }
                }
                
                ins {
                    text-decoration: none;
                    margin: 0;
                }
            }
        }
    }
    
    &.widget_products,
    &.widget_recently_viewed_products,
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .product-title {
                    color: $default-heading-color;
                    font-family: $default-heading-font;
                    font-size: 16px;
                    font-weight: 400;
                    text-transform: uppercase;
                    margin: 10px 0 3px;
                }
                
                .amount {
                    color: $default-text-color;
                    font-size: 13px;
                }
            }
        }
    }
    
    &.widget_recently_viewed_products {
        
        ul {
            
            li {
                
                del,
                > .amount {
                    display: block;
                    margin-top: 10px;
                }
            }
        }
    }
    
    &.widget_recent_reviews {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 7px;
                    margin-bottom: 10px;
                }
            }
        }
        
        a {
            display: block;
            padding: 0 0 0 102px;
        }
        
        .reviewer {
            display: inline-block;
            vertical-align: middle;
            width: auto;
            clear: both;
        }
    }
    
    &.widget_top_rated_products {
        
        ul {
            
            li {
                
                .star-rating {
                    margin-top: 10px;
                    margin-bottom: 12px;
                }
                
                .amount {
                    display: block;
                }
            }
        }
    }
    
    &.widget_product_tag_cloud {

        .eltdf-widget-title {
            margin: 0 0 8px;
        }
        
        .tagcloud {
            
            a {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-family: inherit;
                background-color: transparent;
                font-size: 13px !important;
                
                &:after {
                    content: ',';
                    color: $default-text-color;
                    margin: 0 0 0 4px;
                }
                
                &:last-child {
                    
                    &:after {
                        display: none;
                    }
                }
            }
        }
    }
    
    &.widget_product_search {
        
        .woocommerce-product-search {
            @include eltdfRelativeHolderLayout();
            clear: both;
            
            > div {
                @include eltdfRelativeHolderLayout();
                border: 0;
                border-bottom: 1px solid $default-heading-color;
                box-sizing: border-box;
            }
            
            input,
            button {
                position: relative;
                display: inline-block;
                vertical-align: top;
                float: left;
                height: 52px;
                font-family: inherit;
                font-weight: 400;
                font-size: 12px;
                line-height: 36px;
                color: $default-heading-color;
                text-transform: uppercase;
                background-color: transparent;
                border: 0;
                border-radius: 0;
                outline: 0;
                -webkit-appearance: none;
                box-sizing: border-box;
                @include eltdfTransition(color .2s ease-out);
            }
            
            input {
                width: calc(100% - 50px);
                padding: 0;
                border-right: 1px solid $default-heading-color;
                text-align: center;
                font-family: $default-heading-font;
                letter-spacing: 0.1em;
        
                @include eltdfPlaceholder {
                    color: inherit;
                }
            }
            
            button {
                width: 50px;
                padding: 10px 0;
                font-size: 15px;
                color: $default-text-color;
                cursor: pointer;
                @include eltdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $default-heading-color;
                }
                
                span {
                    display: block;
                    line-height: inherit;
                    
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }
}
/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */