/* ==========================================================================
   WooCommerce Single Product Page styles - begin
   ========================================================================== */

.eltdf-woo-single-page {
	
	&.eltdf-woo-single-thumb-on-left-side {
		
		.eltdf-single-product-content {
			
			.images {
				
				@media only screen and (min-width: $ipad-portrait-plus-pixel) {
					
					figure {
						width: 100%;
						margin: 0;
					}
					
					.woocommerce-product-gallery__image {
						
						&:first-child {
							left: 120px;
							width: calc(100% - 120px);
						}
					}
					
					&.woocommerce-product-gallery--with-images {
						
						.woocommerce-product-gallery__image {
							
							&:not(:first-child) {
								position: absolute;
								top: 0;
								left: 0;
								width: 100px;
								height: calc(25% + 5px);
								padding: 0 0 20px;
								box-sizing: border-box;
								
								a {
									height: 100%;
									
									img {
										height: 100%;
									}
								}
							}
							
							$thumbnail_numbers: (2, 3, 4, 5, 6, 7, 8, 9);
							@for $i from 0 to length($thumbnail_numbers) {
								$thumbnail_number_value: nth($thumbnail_numbers, $i+1);
								
								&:nth-child(#{$thumbnail_number_value}#{n}) {
									$first_param: (25 * $i)#{'%'};
									$second_param: (5 * $i)#{px};
									$topPos: calc(#{$first_param} + #{$second_param});
									
									top: $topPos;
								}
							}
						}
					}
				}
			}
		}
	}
    
    .product {
	    @include eltdfRelativeHolderLayout();
	    float: none;
	    margin: 0 0 50px;
    }
        
    .eltdf-single-product-content {
        @include eltdfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;
	
	    .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 53%;
		    margin: 0;
		    padding: 0 40px 0 0;
		    float: left;
		    box-sizing: border-box;
		    
		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;
			
			    a {
				    @include eltdfRelativeHolderLayout();
				
				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }
		
		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }
		    
		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;
			    
			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }
			
			    .zoomImg {
				    cursor: crosshair;
			    }
		    }
		    
		    &.woocommerce-product-gallery--with-images {
			
			    .woocommerce-product-gallery__image {
				
				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }
		
		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);
				
				    .woocommerce-product-gallery__image {
					
					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }
    }
	
    .eltdf-single-product-summary {
        position: relative;
        display: inline-block;
        vertical-align: middle;
        width: 47%;
        padding: 0 0 0 40px;
        float: left;
        box-sizing: border-box;
	
	    .eltdf-single-product-title {
		    display: block;
		    margin: 5px 0 0;
	    }
	
	    .price {
		    position: relative;
		    display: block;
		    margin: 12px 0 0;
		    line-height: 1;
		    color: $default-heading-color;
		    font-family: $default-heading-font;
		    font-size: 30px;
		    font-weight: 400;
		    float: left;

		    * {
		        font-size: inherit;
		        line-height: inherit;
		    }
		    
		    del {
		        margin: 0 8px 0 0;
		        color: $default-text-color;
		        float: right;
		        clear: both;
		        font-size: 20px;
		        color: #a2a199;
		        line-height: 1.9em;
		        text-decoration-color: $default-heading-color;
		    }
		    
		    ins {
		        text-decoration: none;
		        float: right;
		        margin-right: 18px;
		    }
	    }
	
	    .woocommerce-product-rating {
		    @include eltdfRelativeHolderLayout();
		    line-height: 1;
		    margin: 13px 0 3px;

		    .woocommerce-review-link {
		    	font-size: 13px;
		    	letter-spacing: 0.05em;
		    	color: $default-text-color;
		    	position: relative;
		    	top: 1px;
		    	margin-left: 4px;
			    pointer-events: none;
		    }
	    }
	
	    .woocommerce-product-details__short-description {
		    @include eltdfRelativeHolderLayout();
		    margin: 28px 0 0;
		
		    p {
			    margin: 0;
		    }
	    }
	
	    .product_meta {
		    margin: 43px 0 0;
		
		    > span:not(.sku) {
			    display: block;
			    margin: 9px 0;
			    font-family: $default-heading-font;
			    color: $default-heading-color;
			    font-weight: 400;
			    letter-spacing: -0.01em;
			    line-height: 1em;
			    font-size: 16px;


			    &:last-child {
			    	margin-bottom:0;
			    }
			
			    a,
			    span {
				    color: $default-text-color;
				    font-family: $default-text-font;
				    color: $default-text-color;
				    letter-spacing: 0;
				    margin-left: 10px;
				    font-size: 15px;
			    }

			    a {

				    &:hover {
					    color: $default-heading-color;
				    }
			    }
		    }
	    }

	    .eltdf-social-share-holder.eltdf-list {
	    	.eltdf-social-title {
	    		font-family: $default-heading-font;
			    color: $default-heading-color;
			    font-weight: 400;
			    letter-spacing: -0.01em;
	    	}
	    }  
	
	    .eltdf-woo-social-share-holder {
		    @include eltdfRelativeHolderLayout();
		    margin: 4px 0 0;
		
		    > * {
			    display: table-cell;
			    vertical-align: top;
		    }
		
		    > span {
			    padding: 0 9px 0 0;
		    }

		    &.eltdf-list {
		    	.eltdf-social-title {
		    		font-family: $default-heading-font;
				    color: $default-heading-color;
				    font-weight: 400;
				    letter-spacing: -0.01em;
		    	}
		    }
	    }
	
	    p.stock.out-of-stock,
	    p.stock.in-stock {
		    color: $default-heading-color;
		    font-weight: bold;
		    margin: 18px 0 0;
	    }
	
	    form.cart {
		    @include eltdfRelativeHolderLayout();
		    margin: 36px 0 0;
	    }
	
	    .eltdf-quantity-buttons {
		    margin: 0 15px 0 0;
	    }
    }
	
	.eltdf-accordion-holder {
		@include eltdfRelativeHolderLayout();
		margin: 40px 0 0;
		
		.eltdf-accordion-content {
			padding: 36px 0;
			@include eltdfRelativeHolderLayout();
			
			> h2 {
				display: none;
			}
			
			> p {
				margin: 0;
			}
		}
		
		ul.tabs {
			@include eltdfRelativeHolderLayout();
			list-style: none;
			margin: 0;
			padding: 0;
			border-top: 1px solid $default-border-color;
			border-bottom: 1px solid $default-border-color;
			text-align: center;
			clear: both;
			
			> li {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				
				a {
					position: relative;
					display: inline-block;
					vertical-align: top;
					padding: 13px 15px;
				}
			}
		}
		
		table {
			margin: 0;
			
			tr, th {
				border: 0;
			}
			
			td, th {
				padding: 3px 0;
				vertical-align: middle;
				text-align: left;
				
				p {
					margin: 0;
				}
			}
			
			th {
				width: 1%;
				padding: 0 10px 5px 0;
				font-family: $default-heading-font;
				color: $default-heading-color;
				font-weight: 400;
				font-size: 16px;
			}
			
			td {
				width: 99%;
			}
		}
		
		#reviews {
			@include eltdfRelativeHolderLayout();
			margin: 2px 0 20px;
			
			h2 {
				margin: 8px 0 24px;
			}
			
			ol.commentlist {
				list-style: none;
				
				li {
					margin: 0 0 15px;
					
					&:last-child {
						margin: 0;
					}
				}
				
				.comment_container {
					@include eltdfRelativeHolderLayout();
					
					img {
						float: left;
						width: 70px;
					}
				}
				
				.comment-text {
					padding: 0 0 0 90px;
					
					p {
						margin: 0;
					}

					.woocommerce-review__author {
						font-family: $default-heading-font;
						font-size: 16px;
						font-weight: 400;
						line-height: 30px;
						color: $default-heading-color;
					}

					.woocommerce-review__dash {
						display: none;
					}

					.woocommerce-review__published-date {
						display: inline-block;
						margin: 6px 20px 0;
			            font-size: 12px;
			            position: relative;
						top: -1px;

			            &:before {
			                content:'/';
			                position:absolute;
			                left: -11px;
			            }
					}
				}
			}
			
			.comment-respond {
				margin-top: 15px;
				
				.comment-reply-title {
					margin: 0 0 10px;
				}
				
				p {
					margin: 0;
				}
				
				.stars {
					overflow: hidden;
					
					a {
						position: relative;
						display: inline-block;
						color: transparent;
						font-size: 0;
						margin: 0;
						@include eltdfWooCommerceSingleProductRatingsStyle();
					}
				}
			}
		}
	}
	
	.related.products,
	.upsells.products {
		margin: 30px 0 0;
		
		> h2 {
			display: block;
			margin: 24px 0 32px;
			text-align: left;
		}
		
		.product {
			
			.eltdf-pl-text-wrapper {
				margin-bottom: 0;
			}
		}
	}
}
/* ==========================================================================
   WooCommerce Single Product Page styles - end
   ========================================================================== */